.reviews {
	background: url(../img/reviewsBgr.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	text-align: center;

	&__title {
		color: white;
		font-family: @ptserif;
		font-size: 26px;
		font-weight: normal;
		padding: 66px 0 42px 0;
	}
	&__slider {
		display: inline-block;
	}
	&__item-box {
		display: inline-block;
		background-color: white;
	}
	&__img-box {
		border: 2px solid white;
		display: inline-block;
		position: relative;
		top:-25px;
	}
	&__item-name {
		display: block;
		font-weight: bold;
		font-size: 14px;
		position: relative;
    	top: -18px;
	}
	&__social-link--vk {
		background: url(../img/vk.png);
		background-repeat: no-repeat;
		width: 22px;
		height: 13px;
		display: inline-block;
	}
	&__social-link--fb {
		background: url(../img/fb.png);
		background-repeat: no-repeat;
		width: 18px;
		height: 18px;
		display: inline-block;
	}
	&__bq {
		padding: 10px 40px 0 45px;
		font-size: 16px;
		font-family: @firasans;
		border: 0;
	}
	&__item-date {
		padding: 22px 0 28px 0;
		display: inline-block;
		font-family: @firasans;
		font-size: 14px;
	}
	&__btn-box {
		padding: 53px 0 60px 0;
	}
	&__btn {
		display: inline-block;
		color: white;
		text-decoration: none;
		font-family: @firasans;
		font-size: 14px;
		border: 2px solid white;
		padding: 10px 25px;
		border-radius: 18px;
	}
	&__item-box:nth-child(2) {
		display: none;
	}

	@media screen and (min-width: 420px) {

		&__slider {
			width: 480px;
		}
		&__item-box:nth-child(2) {
			display: inline-block;
			margin-top: 55px;
		}
		&__title {
			font-size: 30px;
		}
	}
}